* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
}

:root {
  --color-white: #fff;
  --color-dark: #252525;
  --color-transparent: #0000008f;
  --color-red: #ff001b;
}

.link {
  color: inherit;
  text-decoration: none;
}

.main-header-container {
  background-color: var(--color-dark);
  background-image: url("background-lines-mobile.22d82665.png");
  background-image: url("background-lines-mobile.22d82665.png"), linear-gradient(var(--color-transparent), var(--color-transparent));
  color: var(--color-white);
  background-position: center;
  max-width: 320px;
  margin: 0 auto;
  padding: 40px 20px 44px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.first-heading {
  display: none;
}

.main-header-list {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 44px;
  font-weight: 500;
  list-style: none;
  display: flex;
}

.main-header-list-left {
  align-items: center;
  gap: 8px;
  display: flex;
}

.logo-link {
  transition: transform .25s;
  display: inline-block;
}

.logo-link:hover, .logo-link:focus {
  transform: scale(1.1);
}

.icon-film {
  max-width: 24px;
  max-height: 24px;
}

.logo-text {
  font-size: 30px;
  display: none;
}

.main-header-list-right {
  text-transform: uppercase;
  gap: 39px;
  font-size: 12px;
  display: flex;
}

.main-header-li {
  list-style: none;
  position: relative;
}

.link:focus {
  outline: none;
}

.main-header-list-right .link:after {
  background-color: var(--color-red);
  content: "";
  width: 0;
  height: 3px;
  transition: width .25s ease-in-out;
  position: absolute;
  bottom: -4px;
  left: 0;
}

.main-header-list-right .link:hover:after, .main-header-list-right .link:focus:after {
  width: 100%;
}

.main-header-form {
  margin-bottom: 16px;
  position: relative;
}

.main-header-input {
  border: none;
  border-bottom: 1px solid var(--color-white);
  color: var(--color-white);
  background: none;
  width: 100%;
  padding: 4px;
  transition: box-shadow .25s;
}

.main-header-input::placeholder {
  opacity: 1;
}

.main-header-input:focus {
  box-shadow: 0 2px var(--color-white);
  outline: none;
}

.icon-search {
  width: 12px;
  height: 12px;
  position: absolute;
  bottom: 6px;
  right: 0;
}

.main-header-fail {
  color: var(--color-red);
  opacity: 0;
}

@media (min-device-pixel-ratio: 2), (resolution >= 192dpi), (resolution >= 2x) {
  .main-header-container {
    background-image: url("background-lines-mobile@2x.30b63605.png");
    background-image: url("background-lines-mobile@2x.30b63605.png"), linear-gradient(var(--color-transparent), var(--color-transparent));
  }
}

@media (width >= 768px) {
  .main-header-container {
    background-image: url("background-lines-tablet.94c6da32.png");
    background-image: url("background-lines-tablet.94c6da32.png"), linear-gradient(var(--color-transparent), var(--color-transparent));
    max-width: 768px;
    padding: 40px 32px 45px;
  }

  .main-header-list {
    margin-bottom: 32px;
  }

  .logo-text {
    display: block;
  }

  .main-header-form {
    max-width: 336px;
    margin: 0 auto 20px;
  }

  .main-header-fail {
    text-align: center;
  }

  @media (min-device-pixel-ratio: 2), (resolution >= 192dpi), (resolution >= 2x) {
    .main-header-container {
      background-image: url("background-lines-tablet@2x.a8e7051f.png");
      background-image: url("background-lines-tablet@2x.a8e7051f.png"), linear-gradient(var(--color-transparent), var(--color-transparent));
    }
  }
}

@media (width >= 1280px) {
  .main-header-container {
    background-image: url("background-lines-desktop.f8b187a8.png");
    background-image: url("background-lines-desktop.f8b187a8.png"), linear-gradient(var(--color-transparent), var(--color-transparent));
    max-width: 1280px;
    padding: 40px 32px 44px;
  }

  .main-header-list-left {
    gap: 10px;
  }

  .main-header-form {
    max-width: 394px;
  }

  @media (min-device-pixel-ratio: 2), (resolution >= 192dpi), (resolution >= 2x) {
    .main-header-container {
      background-image: url("background-lines-desktop@2x.5a78ba33.png");
      background-image: url("background-lines-desktop@2x.5a78ba33.png"), linear-gradient(var(--color-transparent), var(--color-transparent));
    }
  }
}
